<template>
    <div>
        <div class="print">{{SIS}}</div>
        <div class="print">{{SISFLAG}}</div>
        <el-row>
            <el-col :span="3">
                <el-card>
                    <div slot="header" class="title">电捕后煤气含氧量</div>
                    <div>{{arsa_41301}}</div>
                </el-card>
            </el-col>
            <el-col :span="3">
                <el-card>
                    <div slot="header" class="title">入管式炉煤气压力</div>
                    <div>{{prcsa_4201}}</div>
                </el-card>
            </el-col>
            <el-col :span="3">
                <el-card>
                    <div slot="header" class="title">脱苯塔顶压力</div>
                    <div>{{pisa_4204}}</div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import {request} from "../../network/request";
    export default {
        name: "DynamicTableInPM",
        data() {
            return {
                SIS: '',
                SISFLAG: '',

                arsa_41301: '',
                prcsa_4201: '',
                pisa_4204: '',

            }
        },
        created() {
            window.SisRealtime = setInterval(() => {
                setTimeout(request({
                    url: "http://127.0.0.1:8000/sis_realtime_data/",
// method: "post",
                }).then(res=>{
                    this.SIS = res;
                    this.arsa_41301 = res[2];
                    this.prcsa_4201 = res[3];
                    this.pisa_4204 = res[4];
                }).catch(err=>{
// eslint-disable-next-line no-console
                    console.log(err);
                }), 0)
            }, 1000);

            window.SisFlag = setInterval(() => {
                setTimeout(request({
                    url: "http://127.0.0.1:8000/sis_flag_data/",
// method: "post",
                }).then(res=>{
                    this.SISFLAG = res;
                }).catch(err=>{
// eslint-disable-next-line no-console
                    console.log(err);
                }), 0)
            }, 1000);

        },
        destroyed() {
            clearInterval(window.SisRealtime);
            clearInterval(window.SisFlag);
        }

    }
</script>

